import React from 'react';
import {saveAs} from 'file-saver';
import config from './config'

const listPOS = Object.keys(config);

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentFile: 1,
            current: 0,
            words: [],
            result: [],
        };
        let fileName = window.location.search.substr(1);
        fetch(`/nlp_tool/files/${fileName}.json`)
            .then(res => res.json())
            .then(content => {
                if (!content.text.includes("html")) {
                    this.setState({
                        currentFile: parseInt(fileName),
                        words: this.handleContent(content.text),
                    })
                } else {
                    this.setState({
                        currentFile: -1
                    })
                }
            })
    }

    handleContent(text) {
        let words = [];
        text.split(/(?:\n| )+/).map(word => {
            if (word.length > 0) {
                words.push(word)
            }
        });
        return words
    }

    setPOS(pos) {
        this.setState((prevState, props) => {
            prevState.result.push(prevState.words[prevState.current] + "/" + pos + " ");

            if (this.state.current === this.state.words.length - 1) {
                let blob = new Blob(this.state.result, {type: "text/plain;charset=utf-8"});
                saveAs(blob, `${this.state.currentFile}.txt`);
                let currentFile = prevState.currentFile + 1;
                try {
                    window.location.search = `?${currentFile}`;
                    fetch(`/nlp_tool/files/${currentFile}.json`)
                        .then(res => res.json())
                        .then(content => {
                            if (content.text.includes("html")) {
                                this.setState({currentFile: -1})
                            } else {
                                this.setState({words: this.handleContent(content.text)})
                            }
                        });
                    return {
                        currentFile: currentFile,
                        current: 0,
                        words: [],
                        result: []
                    }
                } catch (e) {
                    return {
                        currentFile: -1,
                        words: []
                    }
                }
            } else return {
                current: prevState.current + 1,
                result: prevState.result,
            }
        });
    }

    joinWords() {
        let words = this.state.words;
        let current = this.state.current;
        words[current] += '_' + words[current+1];
        words.splice(current+1, 1);
        this.setState({words: words})
    }

    render() {
        let words = [];
        let count = 0;
        this.state.words.forEach((word, i) => {
            let span = (<span key={i} className={i === this.state.current
                ? "btn-info font-weight-bold ml-3"
                : "text-muted ml-3"}>
                    {word}
            </span>);
            words.push(span);
            if (word === '.') {
                words.push(<br/>);
                count = 0;
            } else if (count === 20) {
                words.push(<br/>);
                count = 0;
            }
            count += 1;
        });
        let choices = listPOS.map(pos =>
            <button className="btn btn-sm btn-outline-info m-1"
                    key={pos} title={config[pos]}
                    onClick={() => this.setPOS.bind(this)(pos)}>
                {pos}
            </button>
        );

        return (
            <div className="container">
                <br/>
                <h5 className="text-info">
                    File: {this.state.currentFile === -1
                    ? "No more file"
                    : this.state.currentFile + ".json"}
                </h5>
                <div style={{flexDirection: "row", flexWrap: "wrap"}}>
                    {words}
                </div>
                <br/>
                <div style={{borderWidth: "2px", borderStyle: "solid",
                            position: "fixed", padding: "10px",
                            bottom: 20, right: 20,
                            maxWidth: "400px", zIndex: 999,
                            display: "flex", flexDirection: "column",
                            flexWrap: "wrap"}}>
                    {choices}
                    <button className="btn btn-sm btn-outline-info m-1"
                            key={'joinWords'} title={'join words'}
                            onClick={() => this.joinWords()}>
                        Join
                    </button>
                </div>
            </div>
        );
    }
}


export default App;
